import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  ViewChild
} from '@angular/core';
import {
  ChartIdentifierConfig,
  MultiValueChartValue
} from '@sofico-framework/ui-kit/types';
import { Chart } from 'chart.js';
import { flatten } from 'lodash';
import { Changes, takeUntilDestroy, UntilDestroy } from 'ngx-reactivetoolkit';
import { combineLatest } from 'rxjs';

@UntilDestroy()
@Component({
  selector: 'sof-line-chart',
  template: `
    <div class="chart-container" style="position: relative; width:100%">
      <canvas #chart></canvas>
    </div>
  `,
  styleUrls: ['./line-chart.component.scss']
})
export class LineChartComponent
  implements OnDestroy, OnChanges, OnInit, AfterViewInit {
  private chart: Chart;
  @ViewChild('chart', { static: true })
  chartElem: ElementRef<HTMLCanvasElement>;

  lineChartData: any;
  @Input() tooltipFn: (
    xLabel: string,
    itemLabel: string,
    yValue: number,
    identifier?: string
  ) => string;
  @Input() identifiersConfig: ChartIdentifierConfig[];
  @Input() suggestedMin: number;
  @Input() suggestedMax: number;
  @Input() chartData: MultiValueChartValue[];
  @Changes('identifiersConfig') identifiersConfig$;
  @Changes('chartData') chartData$;

  constructor(private cdRef: ChangeDetectorRef) {
    this.cdRef.detach();
  }

  ngOnInit(): void {
    combineLatest([this.identifiersConfig$, this.chartData$])
      .pipe(takeUntilDestroy(this))
      .subscribe(([identifiersConfig, values]) => {
        this.setData(
          identifiersConfig as ChartIdentifierConfig[],
          values as MultiValueChartValue[]
        );
      });
  }

  ngOnChanges(): void {}

  ngOnDestroy(): void {
    this?.chart?.destroy();
  }

  ngAfterViewInit(): void {
    const ctx = this.chartElem.nativeElement.getContext('2d');
    this.chart = new Chart(ctx, {
      type: 'line',
      data: this.lineChartData,
      options: {
        tooltips: {
          callbacks: {
            label: (tooltipItem, data) => {
              if (this.tooltipFn) {
                const stackItemLabel =
                  data.datasets[tooltipItem.datasetIndex].label || '';
                return this.tooltipFn(
                  tooltipItem.xLabel as string,
                  stackItemLabel,
                  tooltipItem.value as any,
                  (data.datasets[tooltipItem.datasetIndex] as any).identifier
                );
              }
            }
          }
        },
        aspectRatio: 1,
        responsive: true,
        legend: {
          align: 'start',
          rtl: true
        } as any,
        scales: {
          yAxes: [
            {
              display: true,
              ticks: {
                suggestedMax: this.suggestedMax,
                suggestedMin: this.suggestedMin || 0
              }
            }
          ]
        }
      }
    });
  }

  private setData(
    identifiersConfig: ChartIdentifierConfig[],
    values: MultiValueChartValue[]
  ): void {
    if (!values || !identifiersConfig) {
      return;
    }
    const labels = values.map(v => v?.xLabel);
    const identifiers = [
      ...new Set(
        flatten(values.map(v => v.yValues.map(yValue => yValue.identifier)))
      )
    ];
    const datasets = identifiers.map(identifier => {
      const config = identifiersConfig.find(
        item => item.identifier === identifier
      );
      return {
        label: config?.label,
        fill: false,
        identifier,
        lineTension: 0.1,
        pointBorderWidth: 4,
        backgroundColor: config?.backgroundColor,
        borderColor: config?.backgroundColor,
        data: values.map(
          value =>
            value.yValues.find(yValue => yValue.identifier === identifier)
              ?.value
        )
      };
    });
    this.lineChartData = {
      labels,
      datasets
    };
    if (this.chart) {
      this.chart.data = this.lineChartData;
      this.chart.update();
    }
  }
}
